import React from "react";
import './Help.css';
import Card from './../Card';
import {Grid} from "@material-ui/core";
import classLogo from './../../assets/classroom-logo.png';
import lessonLogo from './../../assets/lesson-logo.png';
import chatLogo from './../../assets/chat-logo.png';
import canvasLogo from './../../assets/canvas-logo.png';
import { NavLink } from "react-router-dom";

const Help = () => {
  return (
    <div>
      <h1 className="header">Help</h1>
      <Grid container space={8} style={{paddingLeft:'0%', paddingRight:'0%'}} >
        <Grid item xs={12} sm={6} style={{padding:'5%'}}> 
          <NavLink to="/mentor_react/help/classroom" style={{ textDecoration: 'none', color:'black' }}>
          <Card title="Classroom" img={classLogo} 
          description="The classroom contains all the lessons in the tutorial group in a module. 
          All the reflections and chats in this tutorial group will be stored here."/>
          </NavLink>
        </Grid>
        <Grid item xs={12} sm={6} style={{padding:'5%'}} > 
        <NavLink to="/mentor_react/help/lesson" style={{ textDecoration: 'none', color:'black' }}>
          <Card title="Lesson" img={lessonLogo}
            description="The lesson contains all reflections and discussions in a tutorial group."
          />
          </NavLink>
        </Grid>
        <Grid item xs={12} sm={6} style={{padding:'5%'}}> 
        <NavLink to="/mentor_react/help/chat" style={{ textDecoration: 'none', color:'black' }}>
          <Card title="Chat" img={chatLogo}
            description="The chat contains peer-to-peer and instructor-to-peer conversations."
          />
          </NavLink>
        </Grid>
        <Grid item xs={12} sm={6} style={{padding:'5%'}}> 
        <NavLink to="/mentor_react/help/canvas" style={{ textDecoration: 'none', color:'black' }}>
          <Card title="Canvas" img={canvasLogo}
            description="The canvas contains all the illustrations made during a call."
          />
          </NavLink>
        </Grid>
      </Grid>
    </div>
  );
};

export default Help;