import React from "react";
import './Introduction.css';
import mentor_logo from './../../assets/mentor_icon.png';
import studentlesson2 from './../../assets/studentlesson2.PNG';
import studentlesson3 from './../../assets/studentlesson3.PNG';
import studentlesson4 from './../../assets/studentlesson4.PNG';
import studentlesson5 from './../../assets/studentlesson3.PNG';
import studentlesson6 from './../../assets/studentlesson4.PNG';
const LessonHelp = () => {
  return (
    <div style={{display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',flexDirection:'column'}}>
      <h1 className="header">Classroom Help</h1>
      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Enter a Reflection</u></h3>
      <img src={studentlesson2} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      1. Click on the "Enter Reflections" tab on the bottom.
      </h2>
      <img src={studentlesson3} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      2. Enter your reflections based on the questions given. Click on the "Submit" button when completed.
      </h2>
      <img src={studentlesson4} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}>
      3. Reflections entered successfully.
      </h2>


      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Comment on a reflection</u></h3>
      <img src={studentlesson4} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      1. Click on the "View Reflections" tab on the bottom. Click on the bottom of the reflection post.
      </h2>
      <img src={studentlesson5} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      2. Enter your comment on the input text bar on the bottom of the screen and click send.
      </h2>
      <img src={studentlesson6} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}>
      3. Comment entered successfully.
      </h2>
      </div>
  );
};

export default LessonHelp;