import React from "react";
import './Introduction.css';
import mentor_logo from './../../assets/mentor_icon.png';
import stafflesson7 from './../../assets/stafflesson7.PNG';
import studentchat1 from './../../assets/studentchat1.PNG';
import studentlesson4 from './../../assets/studentlesson4.PNG';
import studentchat2 from './../../assets/studentchat2.PNG';
import studentchat3 from './../../assets/studentchat3.PNG';
import studentchat4 from './../../assets/studentchat4.PNG';
import studentchat5 from './../../assets/studentchat5.PNG';
import studentchat6 from './../../assets/studentchat6.PNG';
const ChatHelp = () => {
  return (
    <div style={{display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',flexDirection:'column'}}>
      <h1 className="header">Classroom Help</h1>
      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Chat with peers</u></h3>
      <img src={studentlesson4} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      1. Click on the "Enter Reflections" tab on the bottom.
      </h2>
      <img src={stafflesson7} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      2. Click on the icon of student who you are interested to chat with. Confirm by clicking on the "Yes" button.
      </h2>
      <img src={studentchat1} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}>
      3. Chat created successfully.
      </h2>


      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Inserting a PDF file</u></h3>
      <img src={studentchat2} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      1. Click on upload button on the bottom left hand corner of the screen.
      </h2>
      <img src={studentchat3} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      2. Insert a PDF file from your phone storage or external storage.
      </h2>
      <img src={studentchat4} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}>
      3. PDF inserted successfully.
      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Viewing a PDF file</u></h3>
      <img src={studentchat4} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      1. Click on the PDF file of interest.
      </h2>
      <img src={studentchat5} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      2. Click on the arrow buttons on the bottom of the screen to navigate through the pages of PDF. 
      </h2>
      <img src={studentchat6} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}>
      3. Click on the bottom center button to jump to page of interest.
      </h2>
      </div>
  );
};

export default ChatHelp;