import React from "react";
import Flippy, { FrontSide, BackSide } from 'react-flippy';
const Card = (props) => {
  return (
    <Flippy
    flipOnHover={true} // default false
    flipOnClick={false} // default false
    flipDirection="horizontal" // horizontal or vertical
    //ref={(r) => this.flippy = r} // to use toggle method like this.flippy.toggle()
    // if you pass isFlipped prop component will be controlled component.
    // and other props, which will go to div
    style={{ minWidth: '200px', height: '200px', borderRadius:'25px', cursor:'pointer' }} /// these are optional style, it is not necessary
  >
    <FrontSide
      style={{
        backgroundColor: '#0047b6',borderRadius:'25px' 
      }}
    >
      <img src={props.img} alt="img" style={{ padding:'4%', maxWidth: '100%',height: '100%',display: 'block',marginLeft: 'auto', marginRight: 'auto'  }} class="center"/>
      {/*<h1 style={{textAlign:'center', fontSize:'3vw', paddingTop:'12px'}}>{props.title}</h1>*/}
    </FrontSide>
    <BackSide
      style={{ backgroundColor: '#0047b6',borderRadius:'25px',  }}>
      <div style={{padding:'5%', maxWidth: '100%',height: '100%',textAlign:'justify', fontSize:'1.4vw', wordWrap: 'break-word', }}>{props.description}</div>
    </BackSide>
  </Flippy>
  );
};

export default Card;