import React from "react";
import './Introduction.css';
import mentor_logo from './../../assets/mentor_icon.png';
import callButton from './../../assets/callButton.png';
import canvasPosition from './../../assets/canvasPosition.png';
import clearButton from './../../assets/clearButton.png';
import colorPaletteButton from './../../assets/colorPaletteButton.png';
import panButton from './../../assets/panButton.png';
import pdfButton from './../../assets/pdfButton.png';
import pointerButton from './../../assets/pointerButton.png';
import thicknessSlider from './../../assets/thicknessSlider.png';
import undoButton from './../../assets/undoButton.png';
import zoomButton from './../../assets/zoomButton.png';
const CanvasHelp = () => {
  return (
    <div style={{display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',flexDirection:'column'}}>
      <h1 className="header">Canvas Help</h1>
      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Pan Button</u></h3>
      <img src={panButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Select this button to activate the pan mode and navigate around the canvas area.      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Pointer Button</u></h3>
      <img src={pointerButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      The pointer button allows you to point to anywhere on the canvas and that pointer will appear on the peer device.
      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Zoom Button</u></h3>
      <img src={zoomButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Click on the zoom in/out button to zoom in and out.      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>PDF Button</u></h3>
      <img src={pdfButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Click on the PDF button to upload any image of the uploaded PDF files onto the canvas. All the files shared in the Chat page will appear in this section.
      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Color Palette Button</u></h3>
      <img src={colorPaletteButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Select the button to customise the colour of the drawing pen.
      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Thickness Slider</u></h3>
      <img src={thicknessSlider} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Drag along the slider to thicken or thinnen the drawing thickness.
      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Undo Button</u></h3>
      <img src={undoButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Click on it to reverse previous drawing.      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Clear Button</u></h3>
      <img src={clearButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Click on it to clear the canvas.
      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Viewing the current position on the canvas</u></h3>
      <img src={canvasPosition} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      This section at the bottom shows your current position in the canvas. Use this to navigate around the canvas.
      </h2>

      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Call Button</u></h3>
      <img src={callButton} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'3em'}}> 
      Click on it to switch between voice call and canvas drawing page.
      </h2>
      </div>
  );
};

export default CanvasHelp;