import "./App.css";
import Hamburger from "./components/Hamburger";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Introduction from "./components/Pages/Introduction";
import Help from "./components/Pages/Help";
import CanvasHelp from "./components/Pages/CanvasHelp";
import ChatHelp from "./components/Pages/ChatHelp";
import ClassroomHelp from "./components/Pages/ClassroomHelp";
import LessonHelp from "./components/Pages/LessonHelp";
function App() {
  return (
    <div >
      <Router>
        <Hamburger />

        <div className="pages">
          <Switch>
            <Route exact path="/mentor_react" component={Introduction} />
            <Route exact path="/mentor_react/introduction" component={Introduction} />
            <Route exact path="/mentor_react/help" component={Help} />
            <Route exact path="/mentor_react/help/canvas" component={CanvasHelp} />
            <Route exact path="/mentor_react/help/chat" component={ChatHelp} />
            <Route exact path="/mentor_react/help/classroom" component={ClassroomHelp} />
            <Route exact path="/mentor_react/help/lesson" component={LessonHelp} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;