import React from "react";
import './Introduction.css';
import mentor_logo from './../../assets/mentor_icon.png';
import studentclass1 from './../../assets/studentclass1.PNG';
import studentclass2 from './../../assets/studentclass2.PNG';
import studentclass3 from './../../assets/studentclass3.PNG';
const ClassroomHelp = () => {
  return (
    <div style={{display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',flexDirection:'column'}}>
      <h1 className="header">Classroom Help</h1>
      <h3 style={{marginRight:'20%', marginLeft:'20%', fontSize:'4rem', textAlign:'justify', fontWeight:'normal'}}><u>Joining a class</u></h3>
      <img src={studentclass1} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      1. Click on the icon on the top right corner to join a class.
      </h2>
      <img src={studentclass2} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}> 
      2. Enter the room id of the classroom. Press the "Join" button.
      </h2>
      <img src={studentclass3} alt="Logo" />
      <h2 style={{marginRight:'20%', marginLeft:'20%', fontSize:'2rem', textAlign:'justify', fontWeight:'normal', marginBottom:'5em'}}>
      3. Class joined successfully
      </h2>
      </div>
      
  );
};

export default ClassroomHelp;